












































































































































import { Component, Vue, Prop, Watch, Mixins } from 'vue-property-decorator';
import { isWebPSupported } from '../../asset-manager';
import { Character, Part } from '../../models/character';
import { IRenderable } from '../../models/renderable';
import { positions } from '../../models/constants';
import Toggle from '../Toggle.vue';
import Parts from './character/parts.vue';
import { PanelMixin } from './panelMixin';

@Component({
	components: {
		Toggle,
		Parts,
	},
})
export default class CharacterPanel extends Mixins(PanelMixin) {
	@Prop({ required: true, type: Boolean }) private readonly vertical!: boolean;
	@Prop({ type: Character, required: true }) private character!: Character;
	@Prop({ required: true, type: Boolean }) private readonly nsfw!: boolean;

	private panelForParts: Part | null = null;
	private isWebPSupported: boolean | null = null;

	@Watch('character')
	public reset() {
		this.panelForParts = null;
	}

	private async created() {
		this.isWebPSupported = await isWebPSupported();
	}

	private get positionNames(): string[] {
		return positions;
	}

	private get parts(): string[] {
		return this.character
			.getParts()
			.filter(part => this.character.hasMultiple(part, this.nsfw));
	}

	private get hasMultipleStyles(): boolean {
		return (
			this.character.data.styles.filter(style => !style.nsfw || this.nsfw)
				.length > 1
		);
	}

	private get hasMultiplePoses(): boolean {
		return (
			this.character.poses.filter(style => !style.nsfw || this.nsfw).length > 1
		);
	}

	private captialize(str: string) {
		return str.charAt(0).toUpperCase() + str.substring(1);
	}
}

export interface MoveObject {
	object: IRenderable;
	move: 'Forward' | 'Backward' | 'Back' | 'Front' | 'Delete';
}
